import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Form, Button, Alert, Container, Row, Col, Spinner, Toast } from 'react-bootstrap';
import { ethers } from 'ethers';
import contractABI from './ContractABI.json';

const SEPOLIA_CHAIN_ID = '0xaa36a7';
const CONTRACT_ADDRESS = process.env.REACT_APP_CONTRACT_ADDRESS;

function App() {
  const [provider, setProvider] = useState(null);
  const [signer, setSigner] = useState(null);
  const [contract, setContract] = useState(null);
  const [account, setAccount] = useState('');
  const [globalAlert, setGlobalAlert] = useState({ message: '', variant: '' });
  const [networkValid, setNetworkValid] = useState(true);
  const [transactionHistory, setTransactionHistory] = useState([]);

  useEffect(() => {
    async function init() {
      if (window.ethereum) {
        try {
          await handleNetworkCheck();
          await connectWallet();
          window.ethereum.on('chainChanged', handleNetworkCheck);
          window.ethereum.on('accountsChanged', connectWallet);
        } catch (error) {
          console.error('Initialization error:', error);
        }
      }
    }
    init();

    return () => {
      window.ethereum?.removeListener('chainChanged', handleNetworkCheck);
      window.ethereum?.removeListener('accountsChanged', connectWallet);
    };
  }, []);

  const isValidAddress = (address) => {
    return ethers.utils.isAddress(address);
  };
  
  const isPositiveNumber = (value) => {
    return /^[0-9]+$/.test(value) && parseInt(value) > 0;
  };

  const handleNetworkCheck = async () => {
    const chainId = await window.ethereum.request({ method: 'eth_chainId' });
    setNetworkValid(chainId === SEPOLIA_CHAIN_ID);
    if (chainId !== SEPOLIA_CHAIN_ID) {
      setGlobalAlert({
        message: 'Please connect to Sepolia network',
        variant: 'danger'
      });
    }
  };

  const connectWallet = async () => {
    if (!window.ethereum) {
      setGlobalAlert({ message: 'Please install MetaMask', variant: 'warning' });
      return;
    }

    try {
      await window.ethereum.request({ method: 'eth_requestAccounts' });
      const _provider = new ethers.providers.Web3Provider(window.ethereum);
      const _signer = _provider.getSigner();
      const acc = await _signer.getAddress();
      
      setProvider(_provider);
      setSigner(_signer);
      setAccount(acc);
      setContract(new ethers.Contract(CONTRACT_ADDRESS, contractABI, _signer));
      setGlobalAlert({ message: 'Wallet connected successfully', variant: 'success' });
    } catch (error) {
      console.error('Connection error:', error);
      setGlobalAlert({ message: 'Wallet connection failed', variant: 'danger' });
    }
  };

  const addTransaction = (status, message, hash) => {
	const explorerLink = hash ? `https://sepolia.etherscan.io/tx/${hash}` : null;
    setTransactionHistory(prev => [
      {
        id: Date.now(),
        status,
        message,
        hash,
		link: explorerLink,
        timestamp: new Date().toISOString()
      },
      ...prev
    ]);
  };

  return (
    <Container className="mt-4">
      <h1 className="text-center mb-4">Physical Asset Tokenization DApp</h1>
      
      {!networkValid && (
        <Alert variant="danger" className="text-center">
          Please switch to Sepolia Network in your wallet
        </Alert>
      )}

      {account && (
        <Alert variant="info" className="text-center">
          Connected account: <strong>{account}</strong>
        </Alert>
      )}

      <Tabs defaultActiveKey="vendor" id="role-tabs" className="mb-3">
        <Tab eventKey="vendor" title="Vendor" disabled={!networkValid}>
          <VendorTab {...{ contract, account, setGlobalAlert, addTransaction }} />
        </Tab>
        <Tab eventKey="warehouse" title="Warehouse" disabled={!networkValid}>
          <WarehouseTab {...{ contract, account, setGlobalAlert, addTransaction }} />
        </Tab>
        <Tab eventKey="trader" title="Trader" disabled={!networkValid}>
          <TraderTab {...{ contract, account, setGlobalAlert, addTransaction }} />
        </Tab>
		<Tab eventKey="utility" title="Utility Functions">
		  <UtilityTab contract={contract} />
		</Tab>
      </Tabs>

	  <TransactionHistory transactions={transactionHistory} />
      <TransactionToasts transactions={transactionHistory} />
      <GlobalAlert alert={globalAlert} setAlert={setGlobalAlert} />
    </Container>
  );
}

const GlobalAlert = ({ alert, setAlert }) => (
  alert.message && (
    <Alert variant={alert.variant} onClose={() => setAlert({ message: '', variant: '' })} dismissible>
      {alert.message}
    </Alert>
  )
);

const TransactionHistory = ({ transactions }) => (
  <Container className="mt-4">
    <h4>Transaction History</h4>
    {transactions.length === 0 ? (
      <p>No transactions yet.</p>
    ) : (
      <table className="table table-striped">
        <thead>
          <tr>
            <th>#</th>
            <th>Status</th>
            <th>Message</th>
            <th>Timestamp</th>
            <th>Explorer</th>
          </tr>
        </thead>
        <tbody>
          {transactions.map((tx, index) => (
            <tr key={tx.id}>
              <td>{index + 1}</td>
              <td>
                <span className={`badge bg-${tx.status === 'success' ? 'success' : 'danger'}`}>
                  {tx.status.toUpperCase()}
                </span>
              </td>
              <td>{tx.message}</td>
              <td>{tx.timestamp}</td>
              <td>
                {tx.link ? (
                  <a href={tx.link} target="_blank" rel="noopener noreferrer">
                    View on Etherscan
                  </a>
                ) : (
                  '-'
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    )}
  </Container>
);

const TransactionToasts = ({ transactions }) => (
  <div className="toast-container position-fixed bottom-0 end-0 p-3">
    {transactions.map(tx => (
      <Toast key={tx.id} bg={tx.status} autohide delay={5000}>
        <Toast.Header closeButton={false}>
          <strong className="me-auto">{tx.status === 'success' ? '✅ Success' : '❌ Error'}</strong>
        </Toast.Header>
        <Toast.Body>
          <div>{tx.message}</div>
          {tx.hash && (
            <small>
              <a 
                href={`https://sepolia.etherscan.io/tx/${tx.hash}`} 
                target="_blank" 
                rel="noopener noreferrer"
              >
                View on Etherscan
              </a>
            </small>
          )}
        </Toast.Body>
      </Toast>
    ))}
  </div>
);

/* ================= Vendor Tab ================= */
function VendorTab({ contract, account, setGlobalAlert, addTransaction }) {
  const [formStates, setFormStates] = useState({
    vendor: { name: '', email: '' },
    token: { name: '', desc: '', value: '', validity: '' },
    wt: { tokenId: '', address: '', quote: '', msgHash: '', signature: '' },
    abortTokenId: '',
    feedbackTokenId: ''
  });
  const [loading, setLoading] = useState({
    vendorReg: false,
    tokenCreate: false,
    wtSelect: false,
    abort: false,
    feedback: false
  });

  const handleSubmit = async (handlerName, handler) => {
    if (!contract || loading[handlerName]) return;
    try {
      setLoading(prev => ({ ...prev, [handlerName]: true }));
      const result = await handler();
      addTransaction('success', `Transaction ${handlerName} completed`, result.hash);
    } catch (error) {
      console.error(`${handlerName} error:`, error);
      const message = error.reason || error.message || 'Transaction failed';
      addTransaction('danger', message);
      setGlobalAlert({ message, variant: 'danger' });
    } finally {
      setLoading(prev => ({ ...prev, [handlerName]: false }));
    }
  };

  return (
    <Container>
      <h3 className="mt-3">Vendor Functions</h3>
      
      <Row className="mt-4 g-4">
        <Col md={6}>
          <Section title="Vendor Registration">
            <Form onSubmit={e => e.preventDefault()}>
              <InputGroup
                label="Name"
                value={formStates.vendor.name}
                onChange={v => setFormStates(prev => ({ ...prev, vendor: { ...prev.vendor, name: v } }))}
                required
              />
              <InputGroup
                label="Email"
                type="email"
                value={formStates.vendor.email}
                onChange={v => setFormStates(prev => ({ ...prev, vendor: { ...prev.vendor, email: v } }))}
                required
              />
              <SubmitButton
                loading={loading.vendorReg}
                onClick={() => handleSubmit('vendorReg', () => 
                  contract.vendorRegistration(formStates.vendor.name, formStates.vendor.email)
                )}
                text="Register Vendor"
              />
            </Form>
          </Section>

          <Section title="Create Token" className="mt-4">
            <Form onSubmit={e => e.preventDefault()}>
              <InputGroup
                label="Token Name"
                value={formStates.token.name}
                onChange={v => setFormStates(prev => ({ ...prev, token: { ...prev.token, name: v } }))}
                required
              />
              <InputGroup
                label="Description"
                value={formStates.token.desc}
                onChange={v => setFormStates(prev => ({ ...prev, token: { ...prev.token, desc: v } }))}
                required
              />
              <InputGroup
                label="Value (wei)"
                type="number"
                value={formStates.token.value}
                onChange={v => setFormStates(prev => ({ ...prev, token: { ...prev.token, value: v } }))}
                required
              />
              <InputGroup
                label="Validity (days)"
                type="number"
                value={formStates.token.validity}
                onChange={v => setFormStates(prev => ({ ...prev, token: { ...prev.token, validity: v } }))}
                required
              />
              <SubmitButton
                loading={loading.tokenCreate}
                onClick={() => handleSubmit('tokenCreate', () => 
                  contract.createToken(
                    formStates.token.name,
                    formStates.token.desc,
                    formStates.token.value,
                    formStates.token.validity
                  )
                )}
                text="Create Token"
              />
            </Form>
          </Section>
        </Col>

        <Col md={6}>
          <Section title="Warehouse Selection">
            <Form onSubmit={e => e.preventDefault()}>
              <InputGroup
                label="Token ID"
                type="number"
                value={formStates.wt.tokenId}
                onChange={v => setFormStates(prev => ({ ...prev, wt: { ...prev.wt, tokenId: v } }))}
                required
              />
              <InputGroup
                label="Warehouse Address"
                value={formStates.wt.address}
                onChange={v => setFormStates(prev => ({ ...prev, wt: { ...prev.wt, address: v } }))}
                //validate={v => ethers.utils.isAddress(v)}
                required
              />
              <InputGroup
                label="Quote (wei)"
                type="number"
                value={formStates.wt.quote}
                onChange={v => setFormStates(prev => ({ ...prev, wt: { ...prev.wt, quote: v } }))}
                required
              />
              <InputGroup
                label="Message Hash"
                value={formStates.wt.msgHash}
                onChange={v => setFormStates(prev => ({ ...prev, wt: { ...prev.wt, msgHash: v } }))}
                required
              />
              <InputGroup
                label="Signature"
                value={formStates.wt.signature}
                onChange={v => setFormStates(prev => ({ ...prev, wt: { ...prev.wt, signature: v } }))}
                required
              />
              <SubmitButton
                loading={loading.wtSelect}
                onClick={() => handleSubmit('wtSelect', () => 
                  contract.WTselection(
                    formStates.wt.tokenId,
                    formStates.wt.msgHash,
                    formStates.wt.signature,
                    formStates.wt.address,
                    { value: formStates.wt.quote }
                  )
                )}
                text="Select Warehouse"
              />
            </Form>
          </Section>

          <Section title="Token Actions" className="mt-4">
            <InputGroup
              label="Abort Token ID"
              type="number"
              value={formStates.abortTokenId}
              onChange={v => setFormStates(prev => ({ ...prev, abortTokenId: v }))}
            />
            <SubmitButton
              loading={loading.abort}
              onClick={() => handleSubmit('abort', () => 
                contract.abortRequest(formStates.abortTokenId)
              )}
              text="Abort Request"
              variant="warning"
            />

            <InputGroup
              label="Feedback Token ID"
              type="number"
              value={formStates.feedbackTokenId}
              onChange={v => setFormStates(prev => ({ ...prev, feedbackTokenId: v }))}
              className="mt-3"
            />
            <SubmitButton
              loading={loading.feedback}
              onClick={() => handleSubmit('feedback', () => 
                contract.setNegativeFeedbackByOriginator(formStates.feedbackTokenId)
              )}
              text="Set Negative Feedback"
              variant="danger"
            />
          </Section>
        </Col>
      </Row>

      <TokenDetails contract={contract} />
    </Container>
  );
}

