/* ================= Utility Tab ================= */
function UtilityTab({ contract }) {
  const [formStates, setFormStates] = useState({
    messageHash: { tokenId: '', quote: '', nonce: '' },
    ethSignedMessageHash: { messageHash: '' },
    verify: { signer: '', tokenId: '', quote: '', messageHash: '', signature: '' },
    splitSignature: { signature: '' }
  });
  const [results, setResults] = useState({
    messageHash: '',
    ethSignedMessageHash: '',
    verify: '',
    splitSignature: { r: '', s: '', v: '' }
  });
  const [error, setError] = useState('');

  const handleGetMessageHash = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const { tokenId, quote, nonce } = formStates.messageHash;
      if (!tokenId) {
        throw new Error('Token ID is required');
      }
      if (quote && nonce) {
        throw new Error('Provide either Quote or Nonce, not both');
      }
      let hash;
      if (quote) {
        hash = await contract.getMessageHash(tokenId, quote);
      } else if (nonce) {
        // Validate nonce format (bytes32)
        if (!ethers.utils.isHexString(nonce, 32)) {
          throw new Error('Nonce must be a valid bytes32 hex string');
        }
        hash = await contract.getMessageHash(tokenId, nonce);
      } else {
        throw new Error('Either Quote or Nonce is required');
      }
      setResults(prev => ({ ...prev, messageHash: hash }));
    } catch (error) {
      console.error('Error getting message hash:', error);
      setError(error.message);
    }
  };

  const handleGetEthSignedMessageHash = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const { messageHash } = formStates.ethSignedMessageHash;
      if (!messageHash) {
        throw new Error('Message Hash is required');
      }
      const hash = await contract.getEthSignedMessageHash(messageHash);
      setResults(prev => ({ ...prev, ethSignedMessageHash: hash }));
    } catch (error) {
      console.error('Error getting ETH signed message hash:', error);
      setError(error.message);
    }
  };

  const handleVerify = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const { signer, tokenId, quote, messageHash, signature } = formStates.verify;
      if (!signer || !signature) {
        throw new Error('Signer and Signature are required');
      }
      if (!tokenId && !messageHash) {
        throw new Error('Either Token ID + Quote or Message Hash is required');
      }
      let isValid;
      if (tokenId && quote) {
        isValid = await contract.verify(signer, tokenId, quote, signature);
      } else if (messageHash) {
        isValid = await contract.verify(signer, messageHash, signature);
      }
      setResults(prev => ({ ...prev, verify: isValid ? 'Valid' : 'Invalid' }));
    } catch (error) {
      console.error('Error verifying signature:', error);
      setError(error.message);
    }
  };

  const handleSplitSignature = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const { signature } = formStates.splitSignature;
      if (!signature) {
        throw new Error('Signature is required');
      }
      const [r, s, v] = await contract.splitSignature(signature);
      setResults(prev => ({ ...prev, splitSignature: { r, s, v } }));
    } catch (error) {
      console.error('Error splitting signature:', error);
      setError(error.message);
    }
  };

  return (
    <Container>
      <h3 className="mt-3">Utility Functions</h3>
      {error && <Alert variant="danger">{error}</Alert>}

      <Row className="mt-4 g-4">
        <Col md={6}>
          <Section title="Get Message Hash">
            <Form onSubmit={handleGetMessageHash}>
              <InputGroup
                label="Token ID"
                type="number"
                value={formStates.messageHash.tokenId}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  messageHash: { ...prev.messageHash, tokenId: v }
                }))}
                required
              />
              <InputGroup
                label="Quote (optional)"
                type="number"
                value={formStates.messageHash.quote}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  messageHash: { ...prev.messageHash, quote: v }
                }))}
              />
              <InputGroup
                label="Nonce (optional)"
                value={formStates.messageHash.nonce}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  messageHash: { ...prev.messageHash, nonce: v }
                }))}
              />
              <Button type="submit" variant="primary" className="w-100 mt-2">
                Get Message Hash
              </Button>
            </Form>
            {results.messageHash && (
              <div className="mt-3">
                <strong>Result:</strong>
                <pre className="bg-light p-2 rounded">{results.messageHash}</pre>
              </div>
            )}
          </Section>

          <Section title="Get ETH Signed Message Hash" className="mt-4">
            <Form onSubmit={handleGetEthSignedMessageHash}>
              <InputGroup
                label="Message Hash"
                value={formStates.ethSignedMessageHash.messageHash}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  ethSignedMessageHash: { ...prev.ethSignedMessageHash, messageHash: v }
                }))}
                required
              />
              <Button type="submit" variant="primary" className="w-100 mt-2">
                Get ETH Signed Message Hash
              </Button>
            </Form>
            {results.ethSignedMessageHash && (
              <div className="mt-3">
                <strong>Result:</strong>
                <pre className="bg-light p-2 rounded">{results.ethSignedMessageHash}</pre>
              </div>
            )}
          </Section>
        </Col>

        <Col md={6}>
          <Section title="Verify Signature">
            <Form onSubmit={handleVerify}>
              <InputGroup
                label="Signer Address"
                value={formStates.verify.signer}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  verify: { ...prev.verify, signer: v }
                }))}
                validate={v => ethers.utils.isAddress(v)}
                required
              />
              <InputGroup
                label="Token ID (optional)"
                type="number"
                value={formStates.verify.tokenId}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  verify: { ...prev.verify, tokenId: v }
                }))}
              />
              <InputGroup
                label="Quote (optional)"
                type="number"
                value={formStates.verify.quote}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  verify: { ...prev.verify, quote: v }
                }))}
              />
              <InputGroup
                label="Message Hash (optional)"
                value={formStates.verify.messageHash}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  verify: { ...prev.verify, messageHash: v }
                }))}
              />
              <InputGroup
                label="Signature"
                value={formStates.verify.signature}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  verify: { ...prev.verify, signature: v }
                }))}
                required
              />
              <Button type="submit" variant="primary" className="w-100 mt-2">
                Verify Signature
              </Button>
            </Form>
            {results.verify && (
              <div className="mt-3">
                <strong>Result:</strong>
                <pre className="bg-light p-2 rounded">{results.verify}</pre>
              </div>
            )}
          </Section>

          <Section title="Split Signature" className="mt-4">
            <Form onSubmit={handleSplitSignature}>
              <InputGroup
                label="Signature"
                value={formStates.splitSignature.signature}
                onChange={v => setFormStates(prev => ({
                  ...prev,
                  splitSignature: { ...prev.splitSignature, signature: v }
                }))}
                required
              />
              <Button type="submit" variant="primary" className="w-100 mt-2">
                Split Signature
              </Button>
            </Form>
            {results.splitSignature.r && (
              <div className="mt-3">
                <strong>Result:</strong>
                <pre className="bg-light p-2 rounded">
                  r: {results.splitSignature.r}
                  s: {results.splitSignature.s}
                  v: {results.splitSignature.v}
                </pre>
              </div>
            )}
          </Section>
        </Col>
      </Row>
    </Container>
  );
}

// Helper Components
const Section = ({ title, children, className }) => (
  <div className={`border p-3 rounded-3 ${className || ''}`}>
    <h5>{title}</h5>
    {children}
  </div>
);

const InputGroup = ({ label, type = 'text', value, onChange, validate, required, className }) => (
  <Form.Group className={`mb-3 ${className}`}>
    <Form.Label>{label}</Form.Label>
    <Form.Control
      type={type}
      value={value}
      onChange={e => onChange(e.target.value)}
      isInvalid={validate && !validate(value)}
      required={required}
    />
    {validate && !validate(value) && (
      <Form.Control.Feedback type="invalid">
        Invalid {label.toLowerCase()}
      </Form.Control.Feedback>
    )}
  </Form.Group>
);

const SubmitButton = ({ loading, onClick, text, variant = 'primary' }) => (
  <Button
    variant={variant}
    onClick={onClick}
    disabled={loading}
    className="w-100 mt-2"
  >
    {loading ? (
      <>
        <Spinner animation="border" size="sm" /> Processing...
      </>
    ) : (
      text
    )}
  </Button>
);

const TokenDetails = ({ contract }) => {
  const [tokenId, setTokenId] = useState('');
  const [details, setDetails] = useState(null);
  const [loading, setLoading] = useState(false);

  const fetchDetails = async () => {
    if (!contract || !tokenId) return;
    setLoading(true);
    try {
      const tokenInfo = await contract.tokens(tokenId);
      setDetails({
        name: tokenInfo.name,
        description: tokenInfo.description,
        value: ethers.utils.formatUnits(tokenInfo.initialValue, 'wei'),
        state: ['Created', 'Active', 'Burned'][tokenInfo.state],
        owner: tokenInfo.owner,
        warehouse: tokenInfo.warehouse
      });
    } catch (error) {
      console.error('Error fetching token details:', error);
      setDetails(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Section title="Token Details" className="mt-4">
      <div className="d-flex gap-2">
        <Form.Control
          type="number"
          placeholder="Enter Token ID"
          value={tokenId}
          onChange={e => setTokenId(e.target.value)}
        />
        <Button onClick={fetchDetails} disabled={loading}>
          {loading ? 'Loading...' : 'Fetch Details'}
        </Button>
      </div>
      
      {details && (
        <div className="mt-3">
          <p><strong>Name:</strong> {details.name}</p>
          <p><strong>Description:</strong> {details.description}</p>
          <p><strong>Value:</strong> {details.value} wei</p>
          <p><strong>State:</strong> {details.state}</p>
          <p><strong>Owner:</strong> {details.owner}</p>
          {details.warehouse !== ethers.constants.AddressZero && (
            <p><strong>Warehouse:</strong> {details.warehouse}</p>
          )}
        </div>
      )}
    </Section>
  );
};

export default App;
export { UtilityTab };